.navigation {
    margin: 26px 0;
    display: flex;
  
    a {
      margin: 10px;
      padding: 10px;
      border-radius: 4px;
      position: relative;
    }

    .active {
      &::after {
        content: " ";
        width: 85%;
        height: 5px;
        background: $color-1;
        position: absolute;
        bottom: 1px;
        left: 50%;
        transform: translateX(-50%);
      }
    }
  }